import { NextRequest, NextResponse } from "next/server";
import { getAllTasks, addTask } from "@/lib/storage";
import { getSocketIO } from "@/lib/socket";
import { CreateTaskRequest, TaskApiResponse } from "@/types";

export async function GET() {
  try {
    const tasks = await getAllTasks();

    const response: TaskApiResponse = {
      success: true,
      data: tasks,
    };

    return NextResponse.json(response);
  } catch (error) {
    console.error("‚ùå Get tasks error:", error);
    const response: TaskApiResponse = {
      success: false,
      error: "Failed to fetch tasks",
    };
    return NextResponse.json(response, { status: 500 });
  }
}

export async function POST(request: NextRequest) {
  try {
    const body: CreateTaskRequest = await request.json();
    const { text, username } = body;

    if (!text || !username) {
      const response: TaskApiResponse = {
        success: false,
        error: "Text and username are required",
      };
      return NextResponse.json(response, { status: 400 });
    }

    const newTask = await addTask({
      text: text.trim(),
      username: username.toLowerCase(),
      status: "pending",
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
    });

    // Emit to all connected clients
    const io = getSocketIO();
    if (io) {
      io.emit("taskAdded", newTask);
    }

    const response: TaskApiResponse = {
      success: true,
      data: newTask,
      message: "Task created successfully",
    };

    return NextResponse.json(response, { status: 201 });
  } catch (error) {
    console.error("‚ùå Create task error:", error);
    const response: TaskApiResponse = {
      success: false,
      error: "Failed to create task",
    };
    return NextResponse.json(response, { status: 500 });
  }
}
